<template>
	<div class="main">
		<Nav :cou="1"/>
        <List :list="list" :page="page" :key="num_page"/>
		<Footer/>
	</div>
</template>

<script>
import axios from 'axios'
import Nav from '../components/nav'
import List from '../components/list'
import Footer from '../components/footer'
import slug from 'slug'

export default {
	name: 'MainCategory',
    data(){
        return {
            list: [],
            page: 0,
            num_page: 1,
            country: ['Trung Quc',
                        'Hn Quc',
                        'Nht Bn',
                        'Thi Lan',
                        'u M',
                        'i Loan',
                        'Hng Kng',
                        'n ',
                        'Anh',
                        'Php',
                        'Canada',
                        'Quc Gia Khc',
                        'c',
                        'Ty Ban Nha',
                        'Th Nh K',
                        'H Lan',
                        'Indonesia',
                        'Nga',
                        'Mexico',
                        'Ba lan',
                        'c',
                        'Thy in',
                        'Malaysia',
                        'Brazil',
                        'Philippines',
                        'B o Nha',
                        '',
                        'an Mch',
                        'UAE',
                        'Na Uy',
                        'Thy S',
                        'Chu Phi',
                        'Nam Phi',
                        'Ukraina',
                        ' Rp X t']
        }
    },
    created(){
        window.scrollTo(0,0)
        if(this.$route.query.page){
            this.num_page = Number(this.$route.query.page)
        }
        this.getList()
    },
    methods: {
        async getList(){
            try {
                const result = await axios.get('/api/quoc-gia/' + this.$route.params.country, {params: {page: Number(this.num_page) - 1}})
                if(result.data.success){
                    this.list = result.data.data
                    this.page = Math.floor(result.data.count / 20)
                    if(result.data.count % 20 != 0){
                        this.page++
                    }
                }
            } catch(err){
                console.log(err)
            }
        }
    },
	beforeMount() {
        var quocgia = ''
        this.country.forEach(text => {
            const str = slug(text)
            if(str === this.$route.params.country){
                quocgia = text
                return
            }
        })
		document.title = quocgia
	},
    watch: {
        '$route.query': function(){
            if(this.$route.query.page){
                this.num_page = Number(this.$route.query.page)
                this.getList()
                window.scrollTo(0,0)
                this.list = []
            }
        },
        '$route.params': function(){
            if(this.$route.params.country){
                this.getList()
                window.scrollTo(0,0)
                this.list = []
                this.num_page = 1
                var quocgia = ''
                this.country.forEach(text => {
                    const str = slug(text)
                    if(str === this.$route.params.country){
                        quocgia = text
                        return
                    }
                })
                document.title = quocgia
            }
        }
    },
	components: {
		Nav,
        List,
		Footer
	}
}
</script>

<style scoped>

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

</style>
