<template>
	<div class="main">
		<Nav :cate="1"/>
        <List :list="list" :page="page" :key="num_page"/>
		<Footer/>
	</div>
</template>

<script>
import axios from 'axios'
import Nav from '../components/nav'
import List from '../components/list'
import Footer from '../components/footer'
import slug from 'slug'

export default {
	name: 'MainCategory',
    data(){
        return {
            list: [],
            page: 0,
            num_page: 1,
            category: ['Hnh ng',
                        'Tnh Cm',
                        'Hi Hc',
                        'C Trang',
                        'Tm L',
                        'Hnh S',
                        'Chin Tranh',
                        'Th Thao',
                        'V Thut',
                        'Vin Tng',
                        'Phiu Lu',
                        'Khoa Hc',
                        'Kinh D',
                        'm Nhc',
                        'Thn Thoi',
                        'Ti Liu',
                        'Gia nh',
                        'Chnh kch',
                        'B n',
                        'Hc ng',
                        'Kinh in'],
        }
    },
    created(){
        window.scrollTo(0,0)
        if(this.$route.query.page){
            this.num_page = Number(this.$route.query.page)
        }
        this.getList()
    },
    methods: {
        async getList(){
            try {
                const result = await axios.get('/api/the-loai/' + this.$route.params.category, {params: {page: Number(this.num_page) - 1}})
                if(result.data.success){
                    this.list = result.data.data
                    this.page = Math.floor(result.data.count / 20)
                    if(result.data.count % 20 != 0){
                        this.page++
                    }
                }
            } catch(err){
                console.log(err)
            }
        }
    },
	beforeMount() {
        var theloai = ''
        this.category.forEach(text => {
            const str = slug(text)
            if(str === this.$route.params.category){
                theloai = text
                return
            }
        })
		document.title = theloai
	},
    watch: {
        '$route.query': function(){
            if(this.$route.query.page){
                this.num_page = Number(this.$route.query.page)
                this.getList()
                window.scrollTo(0,0)
                this.list = []
            }
        },
        '$route.params': function(){
            if(this.$route.params.category){
                this.getList()
                window.scrollTo(0,0)
                this.list = []
                this.num_page = 1
                var theloai = ''
                this.category.forEach(text => {
                    const str = slug(text)
                    if(str === this.$route.params.category){
                        theloai = text
                        return
                    }
                })
                document.title = theloai
            }
        }
    },
	components: {
		Nav,
        List,
		Footer
	}
}
</script>

<style scoped>

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

</style>
